import axios from "axios";
import React, { useEffect, useState } from "react";
import "../CSS/currency.css";
import { countries } from "../Api/countries";

function Currency() {
  const [info, setInfo] = useState([]);
  const [input, setInput] = useState(1);
  const [from, setFrom] = useState("inr");
  const [to, setTo] = useState("usd");
  const [option, setOption] = useState([]);
  const [output, setOutput] = useState(0);
  const [flagFromImage, setFlagFromImage] = useState(
    "http://purecatamphetamine.github.io/country-flag-icons/3x2/IN.svg"
  );
  const [flagToImage, setFlagToImage] = useState(
    "http://purecatamphetamine.github.io/country-flag-icons/3x2/US.svg"
  );

  const getFromImage = async (countryCode) => {
    const flgImg = `http://purecatamphetamine.github.io/country-flag-icons/3x2/${countryCode.toUpperCase()}.svg`;
    setFlagFromImage(flgImg);
  };
  const getToImage = async (countryCode) => {
    const flgImg = `http://purecatamphetamine.github.io/country-flag-icons/3x2/${countryCode.toUpperCase()}.svg`;
    setFlagToImage(flgImg);
  };

  useEffect(() => {
    axios
      .get(
        `https://cdn.jsdelivr.net/gh/fawazahmed0/currency-api@1/latest/currencies/${from}.json`
      )
      .then((response) => {
        setInfo(response.data[from]);
      });
  }, [from]);

  useEffect(() => {
    setOption(Object.keys(info));
    var rate = info[to];
    setOutput(input * rate);
  }, [info]);

  function Flip() {
    var tempImage = flagFromImage;
    setFlagFromImage(flagToImage);
    setFlagToImage(tempImage);
    var temp = from;
    setFrom(to);
    setTo(temp);
  }

  const countryOption = Object.entries(countries).map((element, index) => {
    return (
      <option value={element[1].currency_code} key={index}>
        {`(${element[1].currency_code}) - ${element[1].currency_name}`}
      </option>
    );
  });

  return (
    <div>
      <div className="container-fluid">
        <div className="row">
          <div className="col-sm-8 offset-sm-2">
            <h4 className="currency-title">Currency convertor</h4>

            <p className="currency-text">
              Based on the exchange rate of central bank
            </p>

            <div className="currency-converter-box row justify-content-between">
              <div className="country-img d-inline-flex col-12 col-lg-4">
                <img
                  style={{ height: "34px" }}
                  src={flagFromImage}
                  alt=""
                  className="img-fluid"
                />
                <h4 className="currencies mb-0 px-3">
                  <input
                    value={input}
                    onChange={(e) => setInput(e.target.value)}
                    className="form-control currencies-input"
                    type="text"
                    placeholder="Enter currency"
                  />
                </h4>
              </div>
              <div className="currency-option col-12 col-lg-4">
                <div className="dropdown">
                  <select
                    name="currencyForm"
                    value={from}
                    multiple={false}
                    onChange={(e) => {
                      setFrom(e.target.value);

                      Object.entries(countries).filter((element, index) => {
                        if (element[1].currency_code === e.target.value) {
                          getFromImage(element[0]);
                        }
                      });
                    }}
                  >
                    {countryOption}
                  </select>
                </div>
              </div>
            </div>
            <div className="currency-reset">
              <button onClick={() => Flip()}>
                <i className="bi bi-arrow-repeat"></i>
              </button>
            </div>
            <div className="currency-converter-box row justify-content-between">
              <div className="country-img d-inline-flex col-12 col-lg-4">
                <img
                  style={{ height: "34px" }}
                  src={flagToImage}
                  alt=""
                  className="img-fluid"
                />
                <input
                  className="form-control currencies-input"
                  type="text"
                  value={output.toFixed(3)}
                  onChange={(e) => {
                    setOutput(e.target.value);
                  }}
                />
              </div>
              <div className="currency-option col-12 col-lg-4">
                <div className="dropdown">
                  <select
                    name="currencyForm"
                    value={to}
                    multiple={false}
                    onChange={(e) => {
                      setTo(e.target.value);
                      Object.entries(countries).filter((element, index) => {
                        if (element[1].currency_code === e.target.value) {
                          getToImage(element[0]);
                        }
                      });
                    }}
                  >
                    {countryOption}
                  </select>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Currency;
